.competitor-review-table-wrapper {

	.competitor-review-table {
		&-instruction {
			text-align: center;
			margin-bottom: 20px;
		}

		&-row {

			border-left: 10px solid #d6dedf;

			&:nth-child(2n) {
				border-left: 10px solid #f5f5f5;
			}

			&:nth-child(1) {
				border-left: 10px solid #f8dfcf;
			}


			min-height: 50px;

			&:first-child &-cell {

				border-top: solid 1px #CCCCCC;

			}

			&:last-child &-cell {

				border-bottom: solid 1px #CCCCCC;

			}
		}



	}

	&[data-has-buttons-row="true"] {
		.competitor-review-table {

			&-row {

				&:last-child{
					border-left: 10px solid #3b3b3b;
				}
			}

		}

	}

}